.mainPage {
  padding: 0px 187px;
}
.page {
  height: 76px;
  left: 0px;
  top: 73px;
  background: #ffffff;
}
.page h3 {
  padding: 30px 0px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  /* identical to box height */
  color: #000000;
}

.balanceInfo {
  width: 486px;
  height: 188px;
  background: #ffffff;
  box-shadow: 0px 3px 100px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-right: 93px;
}
.balanceInfo div {
  padding: 20px;
}
.balanceInfo div span {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  /* identical to box height */
  color: #800080;
}
.balanceInfo div p {
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 48px;
  /* identical to box height */

  color: #000000;
}
.content {
  display: flex;
  margin: 30px 0px;
}

.withdrawBtn {
  padding: 8px 16px;
  background-color: purple;
  color: white;
  border-radius: 20px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
.content div h3 {
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: #800080;

}
.refCode {
  background: linear-gradient(270deg, #800080 0%, #ed4c5c 100%);
  border-radius: 10px;
  width: 358px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.refCode span {
  color: #800080;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  /* identical to box height */
  letter-spacing: 1.5em;
  background: #fff;
  text-align: center;
  padding: 12px 29px;
  border-radius: 10px;
}
h2 {
  padding-bottom: 5px;
  padding: 5px 0px;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  /* identical to box height */
  color: #800080;
}
.howdoesitwork {
  width: 764px;
  height: 332px;
}
.linkSection {
  display: flex;
  flex-wrap: wrap;
}
.link {
  display: flex;
  align-items: center;
}
.profile {
  background: rgba(217, 217, 217, 0.25);
  border-radius: 40px;
  width: 65px;
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imgFriends {
  width: 32px;
  height: 16px;
}
.textContent {
  padding: 15px 20px;
}
.heading {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}
.para {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  width: 272px;
  height: 48px;

  color: #4d4d4d;
}

.whoEnrolled {
  padding: 60px 0px 10px 0px;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */

  color: #800080;
}

.termsCon {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height */

  color: #800080;
}

@media only screen and (max-width: 426px) {
  .mainPage {
    padding: 0px 20px;
  }
  .content {
    display: flex;
    flex-direction: column;
  }
  .balanceInfo {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
  .balanceInfo div {
    width: 50%;
  }
  .balanceInfo div span {
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 21px;
    /* identical to box height */

    color: #800080;
  }
  .balanceInfo div p {
    font-weight: 500;
    font-size: 32px;
    line-height: 48px;
    /* identical to box height */

    color: #000000;
  }
  .withdrawBtn {
    padding: 5px 7px;
    background-color: purple;
    color: white;
    border-radius: 20px;
    font-weight: 500;
    font-size: 10px;
    line-height: 24px;
  }
  .content div h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 36px;
    /* identical to box height */

    color: #800080;
  }
  .refCode {
    background: linear-gradient(270deg, #800080 0%, #ed4c5c 100%);
    border-radius: 10px;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .refCode span {
    color: #800080;
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 30px;
    /* identical to box height */
    letter-spacing: 1.5em;
    background: #fff;
    text-align: center;
    padding: 12px 12px;
    border-radius: 10px;
  }
  .linkSection {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .howdoesitwork {
    width: 100%;
    /* height: 332px; */
  }
  .howdoesitwork h2 {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 36px;
    /* identical to box height */

    color: #800080;
  }
  .imgFriends {
    height: 10px;
    width: 10px;
  }
  .heading {
    font-weight: 500;
    font-size: 15px;
    line-height: 24px;
    /* identical to box height */

    color: #000000;
  }
  .howdoesitwork {
    width: 100%;
    height: auto;
  }
  .linkSection {
    display: flex;
    flex-direction: column;
  }
  .link {
    display: flex;
    align-items: center;
  }
  .profile {
    background: rgba(1, 0, 0, 0.25);
    border-radius: 20px;
    width: 55px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .profile svg {
    width: 20px;
    height: 15px;
  }

  .textContent {
    padding: 10px 0px;
    margin-left: 10px;
  }
  .heading {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    font-weight: bold;
  }
  .para {
    font-weight: 400;
    font-size: 13px;
    line-height: 24px;
    width: 100%;
    height: 100%;

    color: #4d4d4d;
  }

  .whoEnrolled {
    padding: 20px 0px 10px 0px;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */

    color: #800080;
  }

  .termsCon {
    font-family: "Poppins";
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */
    margin: 10px 0px;
    color: #800080;
  }
}
