.logoImage{
    height: 63px;
    width: 160px;
}
.navbar{
    padding: 0px 74px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #eeaeae;
}
.active{
    color: #800080;
}
.navbar ul{
    list-style: none;
    display: flex;
}
.navbar ul li{
   padding: 10px;
   font-weight: 500;
}
.profilesvg{
    list-style: none;
    display: flex;
    align-items: center;
}
.profilesvg svg{
    margin-left: 6px;
}
.profilesvg img{
    height: 20px;
    padding-right: 5px;
}
.mobleview{
   display:  none;
}

@media only screen and (max-width: 426px) {
    .navbar ul li{
       display: none;
     }
     .mobleview{
        display:  block;
     }
     .mobleview button{
        display:  block;
        background-color: #800080;
        color: white;
        padding: 10px 15px ;
        border-radius: 10px;
     }
     .mobleview button svg{
        color: white;
     }
     .navbar{
        padding: 30px 10px;
     }
  }